import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/rain',
      component: index
    },
  {
      path: '/rain/*',
      component: index
    },
    {
      name:'success',
      path:'/success',
      component: r => require.ensure([], () => r(require('@/components/success')))
    },
    {
      path:'/',
      redirect:'/rain'
    }
  ]
})



