import {GetCookie} from "../util/index";

function isUa() {
  var ua = window.navigator.userAgent.toLowerCase()
  //判断是不是微信
  if (ua.match(/MicroMessenger/i) == 'micromessenger') return "WeiXin"
  //哪个都不是
  return "false"
}

let CONFIG =  {
  url: document.URL.indexOf('catchme') !== -1 ?  'https://front.catchme.com.cn/' : 'https://front.5zhua.cn/',
  get token(){
    return   process.env.NODE_ENV === 'development' ? 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwczovL2Zyb250LjV6aHVhLmNuL2FwaS9hdXRoL3Rva2VuIiwiaWF0IjoxNTQyMTgxMDE0LCJleHAiOjE1NDIzOTcwMTQsIm5iZiI6MTU0MjE4MTAxNCwianRpIjoiZldmNUtieDN2eE54bHR6dyIsInN1YiI6Mn0.vk1VittsyrxoqOV_0DDkq5mJ3m1viKilTdjiK1EpeRg' : GetCookie('token_')
  },
  ua:isUa()
}

export default CONFIG



