import CONFIG from './../config'

export const getParamByName = (name) => {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i")
  var r = window.location.search.substr(1).match(reg)
  if (r != null) return unescape(r[2]);
  return null
}

export const GetCookie = (name) => {
  var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if (arr = document.cookie.match(reg)) {
    return unescape(arr[2])
  } else {
    return null
  }
}

export const SetCookie = (name, value) => {
  var Days = 30
  var exp = new Date()
  exp.setTime(exp.getTime() + 60 * 60 * 1000*6)//过期时间 60分钟*6
  document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString()
}

export const jsonToStr = (obj) => {
  var str = '';
  if (Object.keys) {
    var keys = Object.keys(obj)
    for (var key in obj) {
      if (key != keys[keys.length - 1]) {
        str += key + '=' + obj[key] + '&';
      } else {
        str += key + '=' + obj[key];
      }
    }
  } else {
    for (var key in obj) {
      str += key + '=' + obj[key] + '&';
    }
  }
  return str;
}


// 删除url中某个参数,并跳转
function funcUrlDel(name) {
  var loca = window.location;
  var baseUrl = loca.origin + loca.pathname + "?";
  var query = loca.search.substr(1);
  if (query.indexOf(name) > -1) {
    var obj = {}
    var arr = query.split("&");
    for (var i = 0; i < arr.length; i++) {
      arr[i] = arr[i].split("=");
      obj[arr[i][0]] = arr[i][1];
    }
    ;
    delete obj[name];
    var url = baseUrl + JSON.stringify(obj).replace(/[\"\{\}]/g, "").replace(/\:/g, "=").replace(/\,/g, "&");
    return url
  };
}

//授权
export const handleAuth = () => {
  return new Promise((resolve, reject)=>{
    var token = getParamByName('token')
    if(token){
      SetCookie('token_',token)
      location.href = funcUrlDel('token')
    }else if(GetCookie('token_')){
      resolve()
    }else{
      window.location.href = `${CONFIG.url}wechat/oauth_scope?callback=${document.URL}`
    }
  })
}



