<template>
  <div class="rain" v-show="showHtml">
    <div>
      <img class="logo" src="./../assets/home/logo.png" alt=""/>
      <div class="content" v-show="!isShowFail">
        <img class="chai" @click="goResult" src="http://res.catchme.com.cn/activity/rain/chai.png" alt=""/>
      </div>
      <div class="content" v-show="isShowFail">
        <div class="fail">
          <img class="fail-img" src="http://res.catchme.com.cn/activity/rain/ed.png" alt="">
          <h3>领取失败</h3>
          <p>{{message}}</p>
        </div>
      </div>
      <img class="lang" src="./../assets/home/lang_qian.png" alt=""/>
      <img class="circle1" src="./../assets/home/1.png" alt=""/>
      <img class="circle2" src="./../assets/home/2.png" alt=""/>
      <img class="circle3" src="./../assets/home/3.png" alt=""/>
    </div>
    <!--<success ref="success" v-show="isShowSuccess" ></success>-->
  </div>
</template>

<script>
  import api from './../api'
  import {getParamByName} from './../util'
  import CONFIG from "../config/index";
//  import success from './success.vue'
export default {
  name: 'index',
  data () {
    return {
//      isShowSuccess:false,
      isShowFail:false,
      message:'您已经领取过该红包了',
      showHtml:false
    }
  },
//  components:{
//    success
//  },
  mounted(){
    if(CONFIG.token){
      this.showHtml = true;
    }
  },
  methods:{
    goResult(){
      var site_id = getParamByName('site_id');
      var code = getParamByName('c');
      //在这里开红包根据请求结果，来跳转对应的页面
      api.getRainGold({site_id:site_id,code:code}).then((res)=>{
//        this.isShowSuccess = true;
        //        this.$refs.success.handleBi(res.data);
        this.$router.push({name:'success',params:{rain:res.data}});
      }).catch((res)=>{
        if(res.status_code == 1017){
          this.$router.push('/success');
        }else{
          this.isShowFail= true;
          this.message = res.message;
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.rain{
  width: 100%;
  height: 100%;
  background: #ff8e96;
  padding: 0.1px;
  position: relative;
  overflow: hidden;
  >div{
    width: 100%;
    height: 100%;
    .logo{
      width: 252px;
      height: 74px;
      margin: 12px 0 0 30px;
    }
    .content{
      width: 100%;
      height: calc(100% - 237px);
      position: relative;
      z-index: 2;
      color: #fff;
      .fail{
        width:750px;
        height:973px;
        position: absolute;
        left: 0;
        top:50%;
        transform: translateY(calc(-50% - 30px));
        img{
          width: 100%;
        }
        h3{
          font-size: 34px;
          line-height: 34px;
          position: absolute;
          width: 100%;
          text-align: center;
          left:0;
          top:663px;
        }
        p{
          position: absolute;
          width: 100%;
          text-align: center;
          left:0;
          font-size: 24px;
          line-height: 24px;
          top:725px;
        }
      }


    }
    .chai{
      width: 750px;
      position: absolute;
      left: 0;
      top:50%;
      transform: translateY(calc(-50% - 30px));
    }
    .lang{
      width: 750px;
      position: absolute;
      bottom: 0;
      left:0;
    }
    .circle1{
      width: 95px;
      position: absolute;
      left: 50%;
      top:-55px;
    }
    .circle2{
      width: 163px;
      position: absolute;
      right: 85px;
      bottom: 46px;
    }
    .circle3{
      width: 92px;
      position: absolute;
      left: 30px;
      bottom: -16px;
    }
  }
}

</style>



